import { useQuery } from "@tanstack/react-query";
import { supabase } from "@/integrations/supabase/client";
import { useTranslation } from "react-i18next";

export interface RestaurantActivity {
  activity_id: string;
  title_zh: string;
  title_en: string;
  title_pt: string;
  intro_zh?: string | null;
  intro_en?: string | null;
  intro_pt?: string | null;
  badge_text_zh?: string | null;
  badge_text_en?: string | null;
  badge_text_pt?: string | null;
  badge_color?: string | null;
}

// Get activities for a specific restaurant
export const useRestaurantActivities = (restaurantId: string) => {
  const { i18n } = useTranslation();

  return useQuery({
    queryKey: ["restaurant-activities", restaurantId],
    queryFn: async () => {
      const { data, error } = await supabase
        .from("activity_restaurants")
        .select(`
          activity_id,
          activities (
            id,
            title_zh,
            title_en,
            title_pt,
            intro_zh,
            intro_en,
            intro_pt,
            badge_text_zh,
            badge_text_en,
            badge_text_pt,
            badge_color,
            start_date,
            end_date
          )
        `)
        .eq("restaurant_id", restaurantId);

      if (error) throw error;

      // Filter only active activities
      const now = new Date();
      const activeActivities = data
        .filter((item: any) => {
          const activity = item.activities;
          if (!activity) return false;
          
          const startDate = new Date(activity.start_date);
          const endDate = activity.end_date ? new Date(activity.end_date) : null;
          
          return startDate <= now && (!endDate || endDate >= now);
        })
        .map((item: any) => ({
          activity_id: item.activity_id,
          title_zh: item.activities.title_zh,
          title_en: item.activities.title_en,
          title_pt: item.activities.title_pt,
          intro_zh: item.activities.intro_zh,
          intro_en: item.activities.intro_en,
          intro_pt: item.activities.intro_pt,
          badge_text_zh: item.activities.badge_text_zh,
          badge_text_en: item.activities.badge_text_en,
          badge_text_pt: item.activities.badge_text_pt,
          badge_color: item.activities.badge_color,
        }));

      return activeActivities as RestaurantActivity[];
    },
    enabled: !!restaurantId,
  });
};

// Get activities for multiple restaurants at once (for list views)
export const useBatchRestaurantActivities = (restaurantIds: string[]) => {
  const { i18n } = useTranslation();

  return useQuery({
    queryKey: ["batch-restaurant-activities", restaurantIds.join(",")],
    queryFn: async () => {
      if (restaurantIds.length === 0) return {};

      const { data, error } = await supabase
        .from("activity_restaurants")
        .select(`
          restaurant_id,
          activity_id,
          activities (
            id,
            title_zh,
            title_en,
            title_pt,
            intro_zh,
            intro_en,
            intro_pt,
            badge_text_zh,
            badge_text_en,
            badge_text_pt,
            badge_color,
            start_date,
            end_date
          )
        `)
        .in("restaurant_id", restaurantIds);

      if (error) throw error;

      // Group by restaurant and filter active activities
      const now = new Date();
      const activitiesByRestaurant: { [key: string]: RestaurantActivity[] } = {};

      data.forEach((item: any) => {
        const activity = item.activities;
        if (!activity) return;

        const startDate = new Date(activity.start_date);
        const endDate = activity.end_date ? new Date(activity.end_date) : null;

        if (startDate <= now && (!endDate || endDate >= now)) {
          if (!activitiesByRestaurant[item.restaurant_id]) {
            activitiesByRestaurant[item.restaurant_id] = [];
          }

          activitiesByRestaurant[item.restaurant_id].push({
            activity_id: item.activity_id,
            title_zh: activity.title_zh,
            title_en: activity.title_en,
            title_pt: activity.title_pt,
            intro_zh: activity.intro_zh,
            intro_en: activity.intro_en,
            intro_pt: activity.intro_pt,
            badge_text_zh: activity.badge_text_zh,
            badge_text_en: activity.badge_text_en,
            badge_text_pt: activity.badge_text_pt,
            badge_color: activity.badge_color,
          });
        }
      });

      return activitiesByRestaurant;
    },
    enabled: restaurantIds.length > 0,
  });
};

// Helper to get localized activity title
export const getLocalizedActivityTitle = (
  activity: RestaurantActivity,
  language: string
): string => {
  if (language === "zh") return activity.title_zh;
  if (language === "pt") return activity.title_pt;
  return activity.title_en;
};

// Helper to get localized badge text or fallback to activity title
export const getLocalizedBadgeText = (
  activity: RestaurantActivity,
  language: string
): string => {
  let badgeText = "";
  if (language === "zh") {
    badgeText = activity.badge_text_zh || activity.title_zh;
  } else if (language === "pt") {
    badgeText = activity.badge_text_pt || activity.title_pt;
  } else {
    badgeText = activity.badge_text_en || activity.title_en;
  }
  return badgeText;
};

// Helper to get localized intro text
export const getLocalizedIntro = (
  activity: RestaurantActivity,
  language: string
): string => {
  if (language === "zh") {
    return activity.intro_zh || "";
  } else if (language === "pt") {
    return activity.intro_pt || "";
  } else {
    return activity.intro_en || "";
  }
};
