import { Skeleton } from "@/components/ui/skeleton";
import { Card, CardContent, CardHeader } from "@/components/ui/card";
import { ChevronLeft } from "lucide-react";
import { Button } from "@/components/ui/button";

const RestaurantDetailSkeleton = () => {
  return (
    <div className="min-h-screen bg-background pb-8">
      {/* Hero Image Section */}
      <div className="relative h-[30vh] sm:h-[35vh] overflow-hidden">
        <Skeleton className="w-full h-full rounded-none" />
        <div className="absolute inset-0 bg-gradient-to-t from-background/80 to-transparent" />
        
        {/* Back Button */}
        <div className="absolute top-4 left-4">
          <Skeleton className="h-10 w-10 rounded-md" />
        </div>
        
        {/* Action Buttons */}
        <div className="absolute top-4 right-4 flex gap-2">
          <Skeleton className="h-10 w-10 rounded-md" />
          <Skeleton className="h-10 w-10 rounded-md" />
        </div>
      </div>

      {/* Main Content */}
      <div className="container mx-auto px-4 -mt-16 relative z-10">
        <Card className="shadow-large">
          <CardHeader>
            <div className="flex flex-col md:flex-row md:items-start md:justify-between gap-4">
              <div className="flex-1">
                {/* Type badges */}
                <div className="mb-3 flex flex-wrap gap-2">
                  <Skeleton className="h-6 w-16 rounded-full" />
                  <Skeleton className="h-6 w-20 rounded-full" />
                  <Skeleton className="h-6 w-14 rounded-full" />
                </div>
                
                {/* Restaurant name */}
                <Skeleton className="h-9 w-64 mb-3" />
                
                {/* Rating and distance */}
                <div className="flex items-center gap-4 mb-2">
                  <Skeleton className="h-5 w-20" />
                  <Skeleton className="h-5 w-24" />
                  <Skeleton className="h-5 w-16" />
                </div>
              </div>
              
              {/* Action buttons */}
              <div className="flex gap-2">
                <Skeleton className="h-10 w-28 rounded-md" />
                <Skeleton className="h-10 w-10 rounded-md" />
              </div>
            </div>
          </CardHeader>
          
          <CardContent className="space-y-6">
            {/* Announcement section */}
            <div className="space-y-2">
              <Skeleton className="h-5 w-24" />
              <Skeleton className="h-16 w-full rounded-md" />
            </div>
            
            {/* Activities section */}
            <div className="space-y-2">
              <Skeleton className="h-5 w-32" />
              <div className="flex gap-2">
                <Skeleton className="h-8 w-24 rounded-full" />
                <Skeleton className="h-8 w-28 rounded-full" />
              </div>
            </div>
            
            {/* Info rows */}
            <div className="space-y-4">
              {/* Address */}
              <div className="flex items-start gap-3">
                <Skeleton className="h-5 w-5 flex-shrink-0" />
                <div className="flex-1 space-y-1">
                  <Skeleton className="h-5 w-full" />
                  <Skeleton className="h-5 w-3/4" />
                </div>
              </div>
              
              {/* Phone */}
              <div className="flex items-center gap-3">
                <Skeleton className="h-5 w-5 flex-shrink-0" />
                <Skeleton className="h-5 w-32" />
              </div>
              
              {/* Hours */}
              <div className="flex items-start gap-3">
                <Skeleton className="h-5 w-5 flex-shrink-0" />
                <div className="flex-1 space-y-1">
                  <Skeleton className="h-5 w-40" />
                  <Skeleton className="h-5 w-48" />
                </div>
              </div>
            </div>
          </CardContent>
        </Card>
        
        {/* Map Section */}
        <Card className="mt-6 shadow-large">
          <CardHeader>
            <Skeleton className="h-6 w-20" />
          </CardHeader>
          <CardContent>
            <Skeleton className="h-[200px] w-full rounded-lg" />
          </CardContent>
        </Card>
        
        {/* Photo Gallery Section */}
        <Card className="mt-6 shadow-large">
          <CardHeader>
            <Skeleton className="h-6 w-24" />
          </CardHeader>
          <CardContent>
            <div className="flex gap-3 overflow-hidden">
              {[1, 2, 3, 4].map((i) => (
                <Skeleton key={i} className="h-24 w-24 rounded-lg flex-shrink-0" />
              ))}
            </div>
          </CardContent>
        </Card>
        
        {/* Comments Section */}
        <Card className="mt-6 shadow-large">
          <CardHeader className="flex flex-row items-center justify-between">
            <Skeleton className="h-6 w-32" />
            <Skeleton className="h-9 w-24 rounded-md" />
          </CardHeader>
          <CardContent className="space-y-4">
            {[1, 2, 3].map((i) => (
              <div key={i} className="flex gap-3">
                <Skeleton className="h-10 w-10 rounded-full flex-shrink-0" />
                <div className="flex-1 space-y-2">
                  <div className="flex items-center gap-2">
                    <Skeleton className="h-4 w-24" />
                    <Skeleton className="h-4 w-16" />
                  </div>
                  <Skeleton className="h-4 w-full" />
                  <Skeleton className="h-4 w-3/4" />
                </div>
              </div>
            ))}
          </CardContent>
        </Card>
      </div>
    </div>
  );
};

export default RestaurantDetailSkeleton;
