import { Badge } from "@/components/ui/badge";
import { Card, CardContent } from "@/components/ui/card";
import { Separator } from "@/components/ui/separator";
import { Button } from "@/components/ui/button";
import { Collapsible, CollapsibleContent, CollapsibleTrigger } from "@/components/ui/collapsible";
import { MapPin, Phone, MessageCircle, Star, ChevronDown, ShoppingBag, Loader2, Camera } from "lucide-react";
import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { useLocalizedContent } from "@/hooks/useLocalizedContent";
import { RestaurantDishGallery } from "@/components/RestaurantDishGallery";
import RestaurantRating from "@/components/RestaurantRating";
import { lazy, Suspense, useState, useRef } from "react";
import { getLocalizedBadgeText, getLocalizedActivityTitle, getLocalizedIntro } from "@/hooks/useRestaurantActivities";

const RestaurantComments = lazy(() => import("@/components/RestaurantComments"));

interface GhostRestaurantStoryProps {
  restaurant: any;
  localizedRestaurant: any;
  restaurantActivities: any[];
  ratingCount: number;
  averageRating: number;
  onTakeawayClick: () => void;
}

export const GhostRestaurantStory = ({
  restaurant,
  localizedRestaurant,
  restaurantActivities,
  ratingCount,
  averageRating,
  onTakeawayClick,
}: GhostRestaurantStoryProps) => {
  const { t, i18n } = useTranslation();
  const { getLocalizedField } = useLocalizedContent();
  const [activitiesOpen, setActivitiesOpen] = useState(false);
  const [announcementOpen, setAnnouncementOpen] = useState(false);
  const commentsSectionRef = useRef<HTMLDivElement>(null);

  return (
    <div className="relative bg-background">
      {/* Photo Gallery Section */}
      <section className="py-8 md:py-12 lg:py-16">
        <div className="container mx-auto max-w-6xl px-4">
          <div className="text-center mb-6 md:mb-8 lg:mb-10">
            <p className="text-[10px] md:text-xs font-medium tracking-[0.2em] md:tracking-[0.25em] uppercase text-primary mb-2 md:mb-3">
              {t("restaurants.gallery", "美食图鉴")}
            </p>
            <h2 className="text-xl md:text-2xl lg:text-3xl font-bold text-foreground flex items-center justify-center gap-2 md:gap-3">
              <Camera className="w-5 h-5 md:w-6 md:h-6 lg:w-7 lg:h-7 text-primary" />
              {t("restaurants.photoWall", "照片墙")}
            </h2>
            <div className="w-12 md:w-16 h-0.5 md:h-1 bg-primary mx-auto mt-3 md:mt-4" />
          </div>
          
          <RestaurantDishGallery restaurantId={restaurant.id} showGallery={(restaurant as any).show_dish_gallery !== false} />
        </div>
      </section>

      {/* Info Section - Contact & Details */}
      <section className="py-8 md:py-12 lg:py-16 bg-muted/30">
        <div className="container mx-auto max-w-4xl px-4">
          <div className="text-center mb-6 md:mb-8 lg:mb-10">
            <p className="text-[10px] md:text-xs font-medium tracking-[0.2em] md:tracking-[0.25em] uppercase text-primary mb-2 md:mb-3">
              {t("restaurants.info", "详情")}
            </p>
            <h2 className="text-xl md:text-2xl lg:text-3xl font-bold text-foreground flex items-center justify-center gap-2 md:gap-3">
              <MapPin className="w-5 h-5 md:w-6 md:h-6 lg:w-7 lg:h-7 text-primary" />
              {t("restaurants.contactInfo", "联系信息")}
            </h2>
            <div className="w-12 md:w-16 h-0.5 md:h-1 bg-primary mx-auto mt-3 md:mt-4" />
          </div>

          <Card className="border-0 shadow-lg md:shadow-xl overflow-hidden">
            <CardContent className="p-4 md:p-6 lg:p-8 space-y-4 md:space-y-6">
              {/* Type tags and rating */}
              <div className="flex flex-wrap items-center gap-2 md:gap-3">
                {localizedRestaurant.type.slice(0, 3).map((type: string, idx: number) => (
                  <Badge key={idx} variant="secondary" className="text-xs md:text-sm">{type}</Badge>
                ))}
                <Badge variant="outline" className="gap-1 text-xs md:text-sm">
                  <ShoppingBag className="w-3 h-3" />
                  {t("restaurants.ghostKitchen")}
                </Badge>
                {ratingCount > 0 && (
                  <div className="flex items-center gap-1 ml-auto">
                    <Star className="w-4 h-4 md:w-5 md:h-5 fill-accent text-accent" />
                    <span className="font-bold text-base md:text-lg">
                      {averageRating.toFixed(1)}
                      <span className="text-muted-foreground font-normal text-xs md:text-sm ml-1">({ratingCount})</span>
                    </span>
                  </div>
                )}
              </div>

              <Separator />

              {/* Contact Info Grid */}
              <div className="grid gap-3 md:gap-4">
                {/* Activities */}
                {restaurantActivities.length > 0 && (
                  <Collapsible
                    open={activitiesOpen}
                    onOpenChange={setActivitiesOpen}
                    className="flex items-start gap-2 md:gap-3 p-3 md:p-4 rounded-lg md:rounded-xl bg-gradient-to-r from-primary/5 to-transparent border border-primary/10"
                  >
                    <Star className="w-4 h-4 md:w-5 md:h-5 text-primary mt-0.5 flex-shrink-0" />
                    <div className="flex-1 min-w-0">
                      <CollapsibleTrigger className="w-full">
                        <div className="flex items-center justify-between">
                          <div className="flex flex-col items-start">
                            <p className="font-semibold text-sm md:text-base text-card-foreground">{t("activities.title")}</p>
                            {!activitiesOpen && (
                              <div className="flex flex-wrap gap-1 md:gap-1.5 mt-1">
                                {restaurantActivities.slice(0, 2).map((activity, idx) => (
                                  <Badge 
                                    key={`activity-${idx}`} 
                                    variant="default" 
                                    className="text-white text-[10px] md:text-xs"
                                    style={{ backgroundColor: activity.badge_color || "hsl(var(--primary))" }}
                                  >
                                    {getLocalizedBadgeText(activity, i18n.language)}
                                  </Badge>
                                ))}
                                {restaurantActivities.length > 2 && (
                                  <Badge variant="secondary" className="text-[10px] md:text-xs">
                                    +{restaurantActivities.length - 2}
                                  </Badge>
                                )}
                              </div>
                            )}
                          </div>
                          <ChevronDown className={`w-4 h-4 md:w-5 md:h-5 text-muted-foreground transition-transform flex-shrink-0 ${activitiesOpen ? 'rotate-180' : ''}`} />
                        </div>
                      </CollapsibleTrigger>
                      <CollapsibleContent className="mt-2 md:mt-3 space-y-1.5 md:space-y-2">
                        {restaurantActivities.map((activity, idx) => {
                          const intro = getLocalizedIntro(activity, i18n.language);
                          return (
                            <Link
                              key={`activity-detail-${idx}`}
                              to={`/activity/${activity.activity_id}`}
                              className="block p-2 md:p-3 rounded-md md:rounded-lg hover:bg-muted/80 transition-colors border border-transparent hover:border-primary/20"
                            >
                              <Badge 
                                variant="default" 
                                className="text-white mr-1.5 md:mr-2 text-[10px] md:text-xs"
                                style={{ backgroundColor: activity.badge_color || "hsl(var(--primary))" }}
                              >
                                {getLocalizedBadgeText(activity, i18n.language)}
                              </Badge>
                              <span className="text-xs md:text-sm text-muted-foreground">
                                {intro || getLocalizedActivityTitle(activity, i18n.language)} ... →
                              </span>
                            </Link>
                          );
                        })}
                      </CollapsibleContent>
                    </div>
                  </Collapsible>
                )}

                {/* Production Address */}
                <div className="flex items-start gap-2 md:gap-3 p-3 md:p-4 rounded-lg md:rounded-xl bg-muted/50 hover:bg-muted/70 transition-colors">
                  <MapPin className="w-4 h-4 md:w-5 md:h-5 text-primary mt-0.5 flex-shrink-0" />
                  <div className="min-w-0">
                    <p className="font-semibold text-sm md:text-base text-card-foreground">{t("restaurants.productionAddress")}</p>
                    <p className="text-xs md:text-sm text-muted-foreground mt-0.5 md:mt-1 break-words">{restaurant.address}</p>
                  </div>
                </div>

                {/* Phone */}
                {restaurant.phone && (
                  <a 
                    href={`tel:${restaurant.phone}`}
                    className="flex items-start gap-2 md:gap-3 p-3 md:p-4 rounded-lg md:rounded-xl bg-muted/50 hover:bg-primary/10 transition-colors cursor-pointer group"
                  >
                    <Phone className="w-4 h-4 md:w-5 md:h-5 text-primary mt-0.5 flex-shrink-0" />
                    <div>
                      <p className="font-semibold text-sm md:text-base text-card-foreground">{t("restaurant.phone")}</p>
                      <p className="text-sm md:text-base text-primary group-hover:underline mt-0.5 md:mt-1">{restaurant.phone}</p>
                    </div>
                  </a>
                )}

                {/* Announcement */}
                {restaurant && (getLocalizedField(restaurant, "announcement") || restaurant.announcement_zh) && (
                  <Collapsible
                    open={announcementOpen}
                    onOpenChange={setAnnouncementOpen}
                    className="flex items-start gap-2 md:gap-3 p-3 md:p-4 rounded-lg md:rounded-xl bg-muted/50"
                  >
                    <MessageCircle className="w-4 h-4 md:w-5 md:h-5 text-primary mt-0.5 flex-shrink-0" />
                    <div className="flex-1 min-w-0">
                      <CollapsibleTrigger className="w-full">
                        <div className="flex items-center justify-between">
                          <p className="font-semibold text-sm md:text-base text-card-foreground">{t("restaurant.announcement")}</p>
                          <ChevronDown className={`w-4 h-4 md:w-5 md:h-5 text-muted-foreground transition-transform flex-shrink-0 ${announcementOpen ? 'rotate-180' : ''}`} />
                        </div>
                      </CollapsibleTrigger>
                      <CollapsibleContent className="mt-2 md:mt-3">
                        <div className="p-3 md:p-4 rounded-md md:rounded-lg bg-primary/5 border border-primary/10">
                          <p className="text-xs md:text-sm text-foreground whitespace-pre-wrap leading-relaxed">
                            {getLocalizedField(restaurant, "announcement") || restaurant.announcement_zh}
                          </p>
                        </div>
                      </CollapsibleContent>
                    </div>
                  </Collapsible>
                )}
              </div>

              {/* Action Button */}
              {restaurant.website_url && (restaurant as any).show_takeaway_button && (
                <>
                  <Separator />
                  <Button 
                    className="w-full h-10 md:h-12 text-sm md:text-base"
                    size="lg"
                    onClick={onTakeawayClick}
                  >
                    <ShoppingBag className="w-4 h-4 md:w-5 md:h-5 mr-2" />
                    {t("restaurant.takeawayOrder")}
                  </Button>
                </>
              )}
            </CardContent>
          </Card>
        </div>
      </section>

      {/* Rating Section */}
      <section className="py-8 md:py-12 lg:py-16">
        <div className="container mx-auto max-w-4xl px-4">
          <div className="text-center mb-6 md:mb-8 lg:mb-10">
            <p className="text-[10px] md:text-xs font-medium tracking-[0.2em] md:tracking-[0.25em] uppercase text-primary mb-2 md:mb-3">
              {t("restaurants.rating", "评分")}
            </p>
            <h2 className="text-xl md:text-2xl lg:text-3xl font-bold text-foreground flex items-center justify-center gap-2 md:gap-3">
              <Star className="w-5 h-5 md:w-6 md:h-6 lg:w-7 lg:h-7 text-primary" />
              {t("restaurants.rateUs", "给我们评分")}
            </h2>
            <div className="w-12 md:w-16 h-0.5 md:h-1 bg-primary mx-auto mt-3 md:mt-4" />
          </div>

          <Card className="border-0 shadow-lg md:shadow-xl overflow-hidden">
            <CardContent className="p-4 md:p-6 lg:p-8">
              <RestaurantRating restaurantId={restaurant.id} />
            </CardContent>
          </Card>
        </div>
      </section>

      {/* Comments Section */}
      <section className="py-8 md:py-12 lg:py-16 bg-muted/30">
        <div className="container mx-auto max-w-4xl px-4">
          <div className="text-center mb-6 md:mb-8 lg:mb-10">
            <p className="text-[10px] md:text-xs font-medium tracking-[0.2em] md:tracking-[0.25em] uppercase text-primary mb-2 md:mb-3">
              {t("restaurants.reviews", "评价")}
            </p>
            <h2 className="text-xl md:text-2xl lg:text-3xl font-bold text-foreground flex items-center justify-center gap-2 md:gap-3">
              <MessageCircle className="w-5 h-5 md:w-6 md:h-6 lg:w-7 lg:h-7 text-primary" />
              {t("restaurants.customerReviews", "用户评价")}
            </h2>
            <div className="w-12 md:w-16 h-0.5 md:h-1 bg-primary mx-auto mt-3 md:mt-4" />
          </div>

          <Card className="border-0 shadow-lg md:shadow-xl overflow-hidden">
            <CardContent className="p-4 md:p-6 lg:p-8" ref={commentsSectionRef}>
              <Suspense fallback={
                <div className="flex items-center justify-center py-8 md:py-12">
                  <Loader2 className="w-6 h-6 md:w-8 md:h-8 animate-spin text-primary" />
                </div>
              }>
                <RestaurantComments restaurantId={restaurant.id} />
              </Suspense>
            </CardContent>
          </Card>
        </div>
      </section>
    </div>
  );
};

export default GhostRestaurantStory;
