import { Star, DollarSign } from "lucide-react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { useComments } from "@/hooks/useComments";
import { useTranslation } from "react-i18next";
import { cn } from "@/lib/utils";

interface RestaurantRatingProps {
  restaurantId: string;
}

const priceRanges = [
  { value: "0-5", label: "0-5€" },
  { value: "5-10", label: "5-10€" },
  { value: "10-15", label: "10-15€" },
  { value: "15-20", label: "15-20€" },
  { value: "20-25", label: "20-25€" },
  { value: "25-30", label: "25-30€" },
  { value: "30-40", label: "30-40€" },
  { value: "40-50", label: "40-50€" },
  { value: "50+", label: "50€+" },
];

const RestaurantRating = ({ restaurantId }: RestaurantRatingProps) => {
  const { t } = useTranslation();

  // Get all comments with ratings
  const { data: comments } = useComments(restaurantId, "restaurant");
  
  // Filter comments that have ratings
  const ratedComments = comments?.filter(c => c.rating_value) || [];

  const averageRating = ratedComments.length
    ? ratedComments.reduce((sum, c) => sum + (c.rating_value || 0), 0) / ratedComments.length
    : 0;

  const ratingDistribution = [5, 4, 3, 2, 1].map((star) => ({
    star,
    count: ratedComments.filter((c) => c.rating_value === star).length,
    percentage: ratedComments.length
      ? ((ratedComments.filter((c) => c.rating_value === star).length / ratedComments.length) * 100).toFixed(0)
      : 0,
  }));

  // Calculate most common price range
  const priceRangeStats = ratedComments.reduce((acc, comment) => {
    if (comment.price_range) {
      acc[comment.price_range] = (acc[comment.price_range] || 0) + 1;
    }
    return acc;
  }, {} as Record<string, number>);

  const mostCommonPriceRange = priceRangeStats && Object.keys(priceRangeStats).length > 0
    ? Object.entries(priceRangeStats).sort((a, b) => b[1] - a[1])[0]
    : null;

  return (
    <Card>
      <CardHeader>
        <CardTitle>{t("restaurant.rating")}</CardTitle>
      </CardHeader>
      <CardContent className="space-y-6">
        {/* Average Rating Display */}
        <div className="flex items-center gap-6">
          <div className="text-center">
            <div className="text-5xl font-bold">{averageRating.toFixed(1)}</div>
            <div className="flex items-center justify-center mt-2">
              {[1, 2, 3, 4, 5].map((star) => (
                <Star
                  key={star}
                  className={cn(
                    "w-5 h-5",
                    star <= Math.round(averageRating)
                      ? "fill-accent text-accent"
                      : "text-muted-foreground"
                  )}
                />
              ))}
            </div>
            <div className="text-sm text-muted-foreground mt-1">
              {ratedComments.length} {t("restaurant.ratingsCount")}
            </div>
            {mostCommonPriceRange && (
              <div className="flex items-center justify-center gap-1 mt-2 text-sm">
                <DollarSign className="w-4 h-4 text-primary" />
                <span className="font-semibold text-foreground">
                  {priceRanges.find(r => r.value === mostCommonPriceRange[0])?.label}
                </span>
              </div>
            )}
          </div>

          {/* Rating Distribution */}
          <div className="flex-1 space-y-2">
            {ratingDistribution.map(({ star, count, percentage }) => (
              <div key={star} className="flex items-center gap-2 text-sm">
                <span className="w-8">{star} ★</span>
                <div className="flex-1 h-2 bg-secondary rounded-full overflow-hidden">
                  <div
                    className="h-full bg-accent transition-all"
                    style={{ width: `${percentage}%` }}
                  />
                </div>
                <span className="w-12 text-right text-muted-foreground">{count}</span>
              </div>
            ))}
          </div>
        </div>

        {/* Info message */}
        <div className="pt-4 border-t text-center text-sm text-muted-foreground">
          {t("restaurant.signInToRate")}
        </div>
      </CardContent>
    </Card>
  );
};

export default RestaurantRating;
